<template>
  <div class="portfolio-container">
    <!-- Particles background -->
    <vue-particles
      id="tsparticles"
      @particles-loaded="particlesLoaded"
      :options="particlesOptions"
    />

    <!-- Portfolio content -->
    <div class="container portfolio-content mt-2" :class="animationClass">
      <div class="row d-flex justify-content-around">
        <div class="col-md-12 text-right text-white">
          <h1 class="text-warning">Services</h1>
          <h3 class="text-white">My Ready Services For You</h3>
          <p class="text-info">
            I specialize in creating stunning and functional web applications
            that cater to your needs. Whether it's a personal project or a
            business solution, I'm here to help you succeed.
          </p>
        </div>
      </div>

      <!-- services Carousel -->
      <Carousel
        v-bind="settings"
        :breakpoints="breakpoints"
        :autoplay="3500"
        :wrap-around="true"
        :pauseAutoplayOnHover="true"
        :transition="500"
      >
        <Slide v-for="service in services" :key="service.id">
          <div class="service-card">
            <div class="service-image-container">
              <img
                class="service-image"
                :src="service.images[0]"
                :alt="service.name"
              />
              <div class="service-overlay" v-if="service.links">
                <div class="service-actions">
                  <a
                    :href="service.links"
                    target="_blank"
                    class="action-btn view-btn"
                  >
                    <i class="fas fa-external-link-alt"></i> View
                  </a>
                </div>
              </div>
            </div>
            <div class="service-info">
              <h5 class="service-category">{{ service.name }}</h5>
              <p
                class="service-short-description"
                v-html="service.shortDescription"
              ></p>
              <p class="service-description" v-html="service.description"></p>

              <div class="service-actions mt-4 mb-2">
                <button
                  @click.stop="openProjectDetails(service)"
                  class="action-btn details-btn"
                >
                  <i class="fas fa-info-circle"></i> Read In Details
                </button>
              </div>
            </div>
          </div>
        </Slide>

        <template #addons>
          <Navigation />
          <Pagination />
        </template>
      </Carousel>
    </div>

    <!-- service Details Modal -->
    <div
      class="modal-overlay"
      v-if="selectedService"
      @click="closeProjectDetails"
    >
      <div class="modal-content" @click.stop>
        <button class="modal-close" @click="closeProjectDetails">Ã—</button>

        <div class="modal-header">
          <h2>{{ selectedService.name }}</h2>
        </div>

        <div class="modal-body">
          <Carousel :wrap-around="true" :transition="500">
            <Slide
              v-for="(image, index) in selectedService.images"
              :key="index"
            >
              <div class="modal-slide">
                <img
                  class="modal-image"
                  :src="image"
                  :alt="`${selectedService.name} - ${index + 1}`"
                />
              </div>
            </Slide>

            <template #addons>
              <Navigation />
              <Pagination />
            </template>
          </Carousel>

          <div class="modal-details">
            <h4>service Overview</h4>
            <p v-html="selectedService.fullDescription"></p>
          </div>
        </div>

        <div class="modal-footer" v-if="selectedService.subject">
          <a
            :href="
              'mailto:tawshiq.rafi02@gmail.com?subject=' +
              selectedService.subject
            "
            class="modal-btn"
            >Send An Email Now!</a
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import serviceData from "@/assets/data/services.json";
import { defineComponent } from "vue";
import { Carousel, Navigation, Pagination, Slide } from "vue3-carousel";
import "vue3-carousel/dist/carousel.css";

export default defineComponent({
  name: "PortfolioSection",
  components: {
    Carousel,
    Slide,
    Pagination,
    Navigation,
  },
  data() {
    return {
      animationClass: "fade-in",
      selectedService: null,
      // Carousel settings
      settings: {
        itemsToShow: 1,
        snapAlign: "center",
      },
      // Responsive breakpoints
      breakpoints: {
        400: {
          itemsToShow: 1.5,
          snapAlign: "center",
        },
        768: {
          itemsToShow: 2.5,
          snapAlign: "center",
        },
        1200: {
          itemsToShow: 3.5,
          snapAlign: "center",
        },
      },
      // Particles configuration
      particlesOptions: {
        background: {
          color: {
            value: "transparent",
          },
        },
        fullScreen: {
          enable: true,
          zIndex: -1,
        },
        fpsLimit: 60,
        interactivity: {
          detectsOn: "window",
          events: {
            onClick: {
              enable: true,
              mode: "push",
            },
            onHover: {
              enable: true,
              mode: "grab",
              parallax: {
                enable: true,
                force: 60,
                smooth: 100,
              },
            },
            touchstart: {
              // Add this to handle touch events properly
              enable: true,
              mode: "push",
            },
            touchmove: {
              // Add this to allow scrolling while touching particles
              enable: true,
              mode: "grab",
            },
          },
          modes: {
            grab: {
              distance: 400,
              links: {
                opacity: 0.8,
                color: "#ffffff",
              },
            },
            push: {
              quantity: 4,
            },
          },
        },
        particles: {
          color: {
            value: "#64ffda",
          },
          links: {
            color: "#64ffda",
            distance: 150,
            enable: true,
            opacity: 0.4,
            width: 1,
          },
          move: {
            direction: "none",
            enable: true,
            outModes: "bounce",
            random: false,
            speed: 2,
            straight: false,
          },
          number: {
            density: {
              enable: true,
              area: 800,
            },
            value: 100,
          },
          opacity: {
            value: 0.5,
            animation: {
              enable: true,
              speed: 1,
              minimumValue: 0.1,
            },
          },
          shape: {
            type: "circle",
          },
          size: {
            value: { min: 1, max: 3 },
            animation: {
              enable: true,
              speed: 2,
              minimumValue: 0.1,
            },
          },
        },
        detectRetina: true,
      },
      services: serviceData.services,
    };
  },
  methods: {
    toggleAnimation() {
      this.animationClass =
        this.animationClass === "fade-in" ? "bounce-in" : "fade-in";
    },
    truncate(text, length) {
      if (text.length <= length) return text;
      return text.substring(0, length) + "...";
    },
    openProjectDetails(service) {
      this.selectedService = service;
      document.body.style.overflow = "hidden"; // Prevent scrolling behind modal
    },
    closeProjectDetails() {
      this.selectedService = null;
      document.body.style.overflow = ""; // Restore scrolling
    },
  },
});
</script>

<style scoped>
/* Import Font Awesome for icons */
@import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.4/css/all.min.css");

.portfolio-container {
  position: relative;
  min-height: 100vh;
  overflow: hidden;
}

.portfolio-content {
  position: relative;
  z-index: 1;
}

/* service Card Styling - Made more attractive */
.service-card {
  background: rgba(17, 34, 64, 0.7);
  border-radius: 12px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  height: 90vh;
  display: flex;
  flex-direction: column;
  margin: 10px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  border: 1px solid rgba(100, 255, 218, 0.1);
  backdrop-filter: blur(5px);
}

.service-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 40px rgba(100, 255, 218, 0.2);
  border-color: rgba(100, 255, 218, 0.3);
}

/* Improved image container */
.service-image-container {
  position: relative;
  /* Made smaller as requested */
  display: flex;
  justify-content: center;
  align-items: center;
}

.service-image {
  width: 100%;
  object-fit: contain;
  /* Better fit for image */
  transition: transform 0.5s ease;
}

.service-card:hover .service-image {
  transform: scale(1.1);
}

.truncate-line {
  display: flex;
  align-items: center;
  width: 100%;
}

.truncate-text {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: inline-block;
  flex: 1;
  position: relative;
  max-width: 100%;
}

.truncate-text::after {
  content: " ...";
  position: absolute;
  right: 0;
  padding-left: 5px;
  color: #64ffda;
}

/* Enhanced overlay with multiple buttons */
.service-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(10, 25, 47, 0.85);
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.service-card:hover .service-overlay {
  opacity: 1;
}

.service-actions {
  display: flex;
  justify-content: center;
  gap: 10px;
}

.action-btn {
  padding: 8px 16px;
  border-radius: 4px;
  font-weight: 600;
  font-size: 0.85rem;
  transition: all 0.3s ease;
  text-decoration: none;
  display: flex;
  align-items: center;
  gap: 5px;
  cursor: pointer;
  border: none;
}

.details-btn {
  background: #64ffda;
  color: #0a192f;
}

.details-btn:hover {
  background: transparent;
  color: #64ffda;
  box-shadow: inset 0 0 0 2px #64ffda;
}

.view-btn {
  background: transparent;
  color: #64ffda;
  box-shadow: inset 0 0 0 2px #64ffda;
}

.view-btn:hover {
  background: #64ffda;
  color: #0a192f;
}

.service-info {
  padding: 1.5rem;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.service-category {
  font-size: 1.1rem;
  font-weight: 600;
  color: #64ffda;
  margin-bottom: 0.5rem;
}

.service-description {
  font-size: 0.9rem;
  color: #a8b2d1;
  line-height: 1.6;
  flex-grow: 1;
  align-items: self-end;
  display: flex;
}

.service-short-description {
  font-size: 0.9rem;
  color: #ff9500;
  line-height: 1.6;
  flex-grow: 1;
  align-items: self-end;
  display: flex;
}

.service-tech {
  margin-top: 0.75rem;
  display: flex;
  align-items: baseline;
  gap: 5px;
  width: 100%;
  min-width: 0;
}

.tech-items-container {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  flex: 1;
}

.tech-label {
  color: #ccd6f6;
  font-size: 0.8rem;
  font-weight: 600;
  margin-right: 5px;
}

.service-tech {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  flex-shrink: 1;
}

.tech-item {
  display: inline;
  color: #64ffda;
  font-size: 0.75rem;
}

.tech-item:not(:last-child)::after {
  content: ", ";
}

.service-tags {
  display: block;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-top: 1rem;
}

.service-tag {
  display: inline-block;
  background: rgba(100, 255, 218, 0.1);
  color: #64ffda;
  font-size: 0.75rem;
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
  margin-right: 0.5rem;
}

.service-tag:last-child {
  margin-right: 0;
}

@media (max-width: 576px) {
  .service-tag {
    margin-bottom: 0;
  }
}

/* Modal styling for service details */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(10, 25, 47, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  padding: 20px;
  backdrop-filter: blur(8px);
}

.modal-content {
  background: linear-gradient(135deg, #172a45 0%, #0a192f 100%);
  border-radius: 12px;
  width: 90%;
  max-width: 800px;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.5);
  position: relative;
  border: 1px solid rgba(100, 255, 218, 0.2);
  animation: modalFadeIn 0.3s ease-out;
}

@keyframes modalFadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.modal-close {
  position: absolute;
  right: 15px;
  top: 15px;
  background: transparent;
  border: none;
  color: #ccd6f6;
  font-size: 24px;
  cursor: pointer;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  transition: all 0.2s ease;
}

.modal-close:hover {
  background: rgba(100, 255, 218, 0.1);
  color: #64ffda;
}

.modal-header {
  padding: 20px 30px;
  border-bottom: 1px solid rgba(100, 255, 218, 0.1);
}

.modal-header h2 {
  margin: 0;
  color: #64ffda;
  font-size: 1.8rem;
}

.modal-body {
  padding: 30px;
}

.modal-image {
  width: 100%;
  height: 300px;
  object-fit: contain;
  border-radius: 8px;
  margin-bottom: 20px;
  background: rgba(10, 25, 47, 0.5);
  padding: 10px;
}

.modal-details h4 {
  color: #ccd6f6;
  margin-bottom: 10px;
  font-size: 1.2rem;
}

.modal-details p {
  color: #a8b2d1;
  line-height: 1.7;
  margin-bottom: 20px;
}

.modal-tech {
  margin-bottom: 20px;
}

.tech-stack {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 10px;
}

.tech-badge {
  background: rgba(100, 255, 218, 0.1);
  color: #64ffda;
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 0.85rem;
  border: 1px solid rgba(100, 255, 218, 0.2);
}

.modal-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-top: 15px;
}

.modal-tag {
  color: #a8b2d1;
  font-size: 0.85rem;
}

.modal-footer {
  padding: 20px 30px;
  display: flex;
  justify-content: center;
  border-top: 1px solid rgba(100, 255, 218, 0.1);
}

.modal-btn {
  background: #64ffda;
  color: #0a192f;
  font-weight: 600;
  text-decoration: none;
  padding: 10px 25px;
  border-radius: 4px;
  transition: all 0.3s ease;
  display: inline-block;
}

.modal-btn:hover {
  background: transparent;
  color: #64ffda;
  box-shadow: inset 0 0 0 2px #64ffda;
}

/* Carousel customization */
:deep(.carousel__slide) {
  padding: 5px;
}

:deep(.carousel__prev),
:deep(.carousel__next) {
  background-color: rgba(100, 255, 218, 0.2);
  border-radius: 50%;
  width: 40px;
  height: 40px;
  color: #64ffda;
}

:deep(.carousel__prev:hover),
:deep(.carousel__next:hover) {
  background-color: rgba(100, 255, 218, 0.4);
}

/* Animations */
.fade-in {
  animation: fadeIn 1s ease-in-out;
}

.bounce-in {
  animation: bounceIn 1s ease-in-out;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes bounceIn {
  0% {
    opacity: 0;
    transform: scale(0.8);
  }

  50% {
    opacity: 1;
    transform: scale(1.05);
  }

  100% {
    transform: scale(1);
  }
}

/* Responsive adjustments */
@media (max-width: 992px) {
  .service-card {
    height: 100vh;
  }

  .modal-content {
    width: 95%;
  }
}

@media (max-width: 576px) {
  .service-card {
    height: 65vh;
  }

  .service-description {
    font-size: 0.8rem;
  }

  .service-short-description {
    font-size: 0.8rem;
  }

  .modal-header h2 {
    font-size: 1.5rem;
  }

  .service-actions {
    flex-direction: column;
    gap: 8px;
  }
}
</style>
